import { useEffect, useState, useRef, useLayoutEffect } from "react";

// Css
import { Reset } from "styled-reset";
import '../App.css';

// Components
import Header from "../common/Header.js";
import Footer from "../common/Footer.js";

// SWIPER
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, EffectFade } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/effect-fade';

// GSAP
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

// Method
function scroll_next_section() {
    let headerHeight = document.getElementById("header").offsetHeight
    let sectionDesc = document.getElementById('section_desc').offsetTop;
    let offsetTop = sectionDesc - headerHeight

    window.scrollTo({
        top: offsetTop,
        behavior: 'smooth' //  
    });
}

function Home() {

    // GSAP - ScrollTrigger
    gsap.registerPlugin(ScrollTrigger);
    window.addEventListener("resize", ScrollTrigger.update);

    //   
    const consumptionContainerRef = useRef(null);
    useLayoutEffect(() => {
        const consumptionContainer = consumptionContainerRef.current;

        let mm = gsap.matchMedia()
        mm.add("(min-width:1025px)", (context) => {
            gsap.to(consumptionContainer, {
                x: 0,
            });
        })
        mm.add("(max-width:1024px)", (context) => {
            gsap.to(consumptionContainer, {
                x: -300,
                scrollTrigger: {
                    trigger: consumptionContainer,
                    start: "10% center",
                    end: "80% center",
                    scrub: 1,
                }
            });
        })
        mm.add("(max-width:640px)", (context) => {
            gsap.to(consumptionContainer, {
                x: -300,
                scrollTrigger: {
                    trigger: consumptionContainer,
                    start: "top center",
                    end: "70% center",
                    scrub: 1,
                }
            });
        })
    })

    //  
    const investContainerRef = useRef(null);
    const investItemListRef = useRef(null);
    const investItemRef = useRef([]);

    useLayoutEffect(() => {
        const investContainer = investContainerRef.current;
        const investItemList = investItemListRef.current;
        const investItem = investItemRef.current;

        let mm2 = gsap.matchMedia()
        mm2.add("(min-width:1441px)", (context) => {
            gsap.set(investItemList, { xPercent: -50 })
            investItem.map(item => {
                gsap.set(item, { opacity: 1 })
            })

            const tl = gsap.timeline({
                scrollTrigger: {
                    target: investItem,
                    trigger: investContainer,
                    start: "40% center", //  
                    end: "80% 90%",
                    scrub: true,
                }
            })
            tl.from(investItem[2], { opacity: 0 }, "act-1")
            tl.from(investItem[4], { opacity: 0 }, "act-1")
            tl.from(investItem[1], { opacity: 0 }, "act-2")
            tl.from(investItem[5], { opacity: 0 }, "act-2")
            tl.from(investItem[0], { opacity: 0 }, "act-3")
            tl.from(investItem[6], { opacity: 0 }, "act-3")
        })

        mm2.add("(max-width:1440px)", (context) => {
            gsap.set(investItemList, { xPercent: 0 })
            investItem.map(item => {
                gsap.set(item, { opacity: 1 })
            })

            const tl = gsap.timeline({
                scrollTrigger: {
                    target: investItemList,
                    trigger: investContainer,
                    start: "center 40%", //  
                    end: "80% center",
                    scrub: 1,
                }
            })
            tl.to(investItemList, { xPercent: -70 })
        })
    })

    //   
    const financeContainerRef = useRef(null);
    const financeItemRef = useRef([]);
    useLayoutEffect(() => {
        const financeContainer = financeContainerRef.current;
        const financeItem = financeItemRef.current;

        const context = gsap.context(() => {
            const tl = gsap.timeline({
                scrollTrigger: {
                    target: financeItem,
                    trigger: financeContainer,
                    start: "40% bottom", //  
                    end: "15% start",
                    scrub: true,
                }
            })
            tl.to(financeItem[0], { left: "-25%" }, "act")
            tl.to(financeItem[1], { right: "-25%" }, "act")
        })

        return () => context.revert()
    })

    
    // State
    let [headerFloat, setHeaderFloat] = useState("");


    // Method
    function checkHeaderFloat() {
        let scrollTop = document.documentElement.scrollTop;
        let header_bottom = document
            .getElementById("header")
            .getBoundingClientRect().bottom;
        if (scrollTop > header_bottom) {
            setHeaderFloat("floating");
        } else {
            setHeaderFloat("");
        }
    }
    function elemAnimation() {
        let scrollTop = document.documentElement.scrollTop;
        const elements = document.querySelectorAll('.elem_animate');

        elements.forEach((element, index) => {
            let { top } = element.getBoundingClientRect();
            top = scrollTop + top
            let windowHeight = window.innerHeight

            if (scrollTop + (windowHeight / 1.05) >= top) {
                element.classList.add('animated');
            }
        });
    };


    // UseEffect
    useEffect(() => {
        checkHeaderFloat();
        elemAnimation();
        window.addEventListener('scroll', checkHeaderFloat);
        window.addEventListener('scroll', elemAnimation);

        // clean-up
        return () => {
            window.removeEventListener('scroll', checkHeaderFloat);
            window.removeEventListener('scroll', elemAnimation);
        };
    }, []);

    // Render
    return (
        <div className="App">
            <Reset />

            {/* Header */}
            <Header headerFloat={headerFloat}></Header>

            {/* Main */}
            <main className="main">

                {/* Section - Intro */}
                <section className="main-sec intro">
                    <div className="intro__linear-bg"></div>
                    <div className="intro__wrap">
                        <div className="intro__top elem_animate">
                            <p className="intro__text">   <br />   </p>
                            <div className="intro__btn-wrap">
                                <a href="/clone_toss_react" className="intro__app-btn">
                                    <img src="https://static.toss.im/png-icons/timeline/applekorea.png" alt="" height="24" width="24" />
                                    <span className="poppins">App Store</span>
                                </a>
                                <a href="/clone_toss_react" className="intro__app-btn">
                                    <img src="https://static.toss.im/png-icons/timeline/googleplay.png" alt="" height="24" width="24" />
                                    <span className="poppins">Google Play</span>
                                </a>
                            </div>
                        </div>
                        <div className="intro__bottom">
                            <button className="intro__bottom-btn" onClick={scroll_next_section}>
                                <svg className="moving-svg"
                                    xmlns="http://www.w3.org/2000/svg"
                                    width="24"
                                    height="24"
                                    viewBox="0 0 24 24"
                                    fill="none"
                                    stroke="currentColor"
                                    strokeWidth="2"
                                    strokeLinecap="round"
                                    strokeLinejoin="round"
                                >
                                    <path d="M19 12l-7 7-7-7" />
                                </svg>
                            </button>
                        </div>
                    </div>
                </section>

                {/* Section - Description */}
                <section id="section_desc" className="main-sec desc">
                    <div className="inner">
                        <div className="main-sec__wrap">
                            <p>
                                        .<br />
                                       ,<br />
                                     .
                            </p>
                        </div>
                    </div>
                </section>

                {/* Section -  */}
                <section className="main-sec consumption">
                    <div className="inner">
                        <div className="main-sec__wrap">
                            <h1 className="main-sec__title elem_animate up"></h1>
                            <p className="main-sec__desc elem_animate up">
                                  ,<br />
                                 <br />
                                
                            </p>
                            <div className="consumption__img-wrap" ref={consumptionContainerRef}>
                                <div className="consumption__img-item first elem_animate up">
                                    <img src={`${process.env.PUBLIC_URL}/imgs/main/consumption_img_2.png`} alt="" className="consumption__img-img" />
                                    <img src={`${process.env.PUBLIC_URL}/imgs/main/consumption_img_bg.png`} alt="" className="consumption__img-bg" />
                                </div>

                                <div className="consumption__img-item second elem_animate up">
                                    <img src={`${process.env.PUBLIC_URL}/imgs/main/consumption_img_1.png`} alt="" className="consumption__img-img" />
                                    <img src={`${process.env.PUBLIC_URL}/imgs/main/consumption_img_bg.png`} alt="" className="consumption__img-bg" />
                                </div>
                            </div>
                            <p className="consumption__desc main-sec__small-desc elem_animate up">
                                    .<br />
                                 ,   ,<br />
                                       .
                            </p>
                        </div>
                    </div>
                </section>

                {/* Section -  */}
                <section className="main-sec transfer">
                    <div className="inner">
                        <div className="main-sec__wrap">
                            <h1 className="main-sec__title elem_animate up"></h1>
                            <p className="main-sec__desc elem_animate up">
                                 <br />
                                  ,<br />
                                  ?
                            </p>
                            <div className="transfer__content-wrap">
                                <ul className="transfer__list">
                                    <li className="transfer__item">
                                        <div className="transfer__item-left elem_animate up">
                                            <p className="transfer__item-title">
                                                  
                                            </p>
                                            <p className="transfer__item-desc--bold">
                                                  <br />
                                                  
                                            </p>
                                            <p className="transfer__item-desc">
                                                    ,<br />
                                                      .
                                            </p>
                                        </div>
                                        <div className="transfer__item-right elem_animate up">
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main//transfer_img_1.png`} alt="" />
                                        </div>
                                    </li>
                                    <li className="transfer__item">
                                        <div className="transfer__item-left elem_animate up">
                                            <p className="transfer__item-title">
                                                 
                                            </p>
                                            <p className="transfer__item-desc--bold">
                                                    <br />
                                                    
                                            </p>
                                            <p className="transfer__item-desc">
                                                       .<br />
                                                      <br />
                                                   .
                                            </p>
                                        </div>
                                        <div className="transfer__item-right elem_animate up">
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/transfer_img_2.png`} alt="" />
                                        </div>
                                    </li>
                                    <li className="transfer__item">
                                        <div className="transfer__item-left elem_animate up">
                                            <p className="transfer__item-title">
                                                 
                                            </p>
                                            <p className="transfer__item-desc--bold">
                                                  ,<br />
                                                  
                                            </p>
                                            <p className="transfer__item-desc">
                                                     .<br />
                                                     .
                                            </p>
                                        </div>
                                        <div className="transfer__item-right elem_animate up">
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/transfer_img_3.png`} alt="" />
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </section>

                {/* Section -  */}
                <section className="main-sec loan">
                    <div className="inner">
                        <div className="main-sec__wrap">
                            <div className="loan__wrap">
                                <h1 className="main-sec__title elem_animate up"></h1>
                                <p className="main-sec__desc elem_animate up">
                                      <br />
                                    1 <br />
                                    
                                </p>

                                <div id="loan-img-slide" className="loan__img-wrap">
                                    <div className=" elem_animate up">
                                        <Swiper
                                            effect={'fade'}
                                            autoplay={{
                                                delay: 3000,
                                                disableOnInteraction: false,
                                            }}
                                            modules={[Autoplay, EffectFade]}
                                        >
                                            <SwiperSlide>
                                                <img src={`${process.env.PUBLIC_URL}/imgs/main/loan_img_1.png`} alt="" className="loan__img-item" />
                                            </SwiperSlide>
                                            <SwiperSlide>
                                                <img src={`${process.env.PUBLIC_URL}/imgs/main/loan_img_2.png`} alt="" className="loan__img-item" />
                                            </SwiperSlide>
                                        </Swiper>

                                        <img src={`${process.env.PUBLIC_URL}/imgs/main/consumption_img_bg.png`} alt="" className="loan__img-bg" />
                                    </div>
                                </div>

                                <div className="loan__desc-wrap elem_animate up">
                                    <div className="loan__desc-bold ">
                                        <p className="elem_animate up"> ,</p>
                                        <p className="elem_animate up"> <span className="gray">,</span></p>
                                        <p className="elem_animate up"> <span className="lightgray">.</span></p>
                                    </div>
                                    <div className="loan__desc-small">
                                              <br />
                                            .<br />
                                        , , ,   .
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                {/* Section -  */}
                <section className="main-sec credit">
                    <div className="inner">
                        <div className="main-sec__wrap">
                            <h1 className="main-sec__title elem_animate up"></h1>
                            <p className="main-sec__desc elem_animate up">
                                   ,<br />
                                 <br />
                                 
                            </p>
                            <div className="credit__content-wrap">
                                <div className="credit__layout elem_animate up">
                                    <div className="credit__layout-item">
                                        <img src={`${process.env.PUBLIC_URL}/imgs/main/credit_img_1.png`} alt="credit 1" className="credit__layout-img" />
                                        <p className="credit__layout-title"> </p>
                                        <p className="credit__layout-desc">
                                             ,   <br />
                                            KCB, NICE      .
                                        </p>
                                    </div>
                                    <div className="credit__layout-item">
                                        <img src={`${process.env.PUBLIC_URL}/imgs/main/credit_img_2.png`} alt="credit 2" className="credit__layout-img" />
                                        <p className="credit__layout-title"> </p>
                                        <p className="credit__layout-desc">
                                            ,    <br />
                                                  .
                                        </p>
                                    </div>
                                    <div className="credit__layout-item">
                                        <img src={`${process.env.PUBLIC_URL}/imgs/main/credit_img_3.png`} alt="credit 3" className="credit__layout-img" />
                                        <p className="credit__layout-title"> </p>
                                        <p className="credit__layout-desc">
                                                 .<br />
                                                 .
                                        </p>
                                    </div>
                                    <div className="credit__layout-item">
                                        <img src={`${process.env.PUBLIC_URL}/imgs/main/credit_img_4.png`} alt="credit 4" className="credit__layout-img" />
                                        <p className="credit__layout-title"> </p>
                                        <p className="credit__layout-desc">
                                              ?<br />
                                                .
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                {/* Section -  */}
                <section className="main-sec invest" ref={investContainerRef}>
                    <div className="inner">
                        <div className="main-sec__wrap">
                            <h1 className="main-sec__title elem_animate up"></h1>
                            <p className="main-sec__desc elem_animate up">
                                ,<br />
                                   
                            </p>

                            <div className="invest__content-wrap elem_animate up">
                                <div className="invest__desc-wrap">
                                    <p className="main-sec__small-desc elem_animate up">
                                          , <br className="hide_1024" />
                                           <br className="hide_1024" />
                                          ,
                                    </p>
                                    <p className="main-sec__small-desc elem_animate up">
                                            <br className="hide_1024" />
                                           <br className="hide_1024" />
                                          
                                    </p>
                                </div>

                                <div className="invest__img-wrap">
                                    <img src={`${process.env.PUBLIC_URL}/imgs/main/invest_img_1.png`} alt="" className="invest__img-img" />
                                    <img src={`${process.env.PUBLIC_URL}/imgs/main/consumption_img_bg.png`} alt="" className="invest__img-bg" />
                                    <div className="invest__img-list" ref={investItemListRef}>
                                        <div className="invest__img-item" ref={ref => investItemRef.current[0] = ref}>
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/invest_img_6.png`} alt="" />
                                        </div>
                                        <div className="invest__img-item" ref={ref => investItemRef.current[1] = ref}>
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/invest_img_8.png`} alt="" />
                                        </div>
                                        <div className="invest__img-item" ref={ref => investItemRef.current[2] = ref}>
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/invest_img_7.png`} alt="" />
                                        </div>
                                        <div className="invest__img-item show" ref={ref => investItemRef.current[3] = ref}>
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/invest_img_5.png`} alt="" />
                                        </div>
                                        <div className="invest__img-item" ref={ref => investItemRef.current[4] = ref}>
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/invest_img_2.png`} alt="" />
                                        </div>
                                        <div className="invest__img-item" ref={ref => investItemRef.current[5] = ref}>
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/invest_img_4.png`} alt="" />
                                        </div>
                                        <div className="invest__img-item" ref={ref => investItemRef.current[6] = ref}>
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/invest_img_3.png`} alt="" />
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <p className="invest__desc main-sec__small-desc elem_animate up">
                                     ,<br />
                                    .
                            </p>
                        </div>
                    </div>
                </section>

                {/* Section -   (  ) */}
                <section className="main-sec finance" ref={financeContainerRef}>
                    <div className="inner">
                        <div className="main-sec__wrap">
                            <div className="finance-text"> <br className="hide show_640" /> </div>
                        </div>
                    </div>

                    <div className="animation-bg left" ref={ref => financeItemRef.current[0] = ref}></div>
                    <div className="animation-bg right" ref={ref => financeItemRef.current[1] = ref}></div>
                </section>

                {/* Section -     */}
                <section className="main-sec fit">
                    <div className="inner">
                        <div className="main-sec__wrap">
                            <p className="main-sec__desc elem_animate up">
                                 <br />
                                  
                            </p>

                            <div className="fit__content-wrap">
                                <div className="fit-section elem_animate up">
                                    <div className="fit-section__item">
                                        <div className="img-wrap">
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/fit_img_1.jpg`} alt="" className="img" />
                                        </div>
                                        <div className="fit-section__title">
                                              <span className="gray"> <br className="hide show_640" />   </span>
                                        </div>
                                        <div className="fit-section__desc">
                                            ,  ,  .<br />
                                                  .<br />
                                                    .
                                        </div>
                                    </div>
                                </div>

                                <div className="fit-section elem_animate up">
                                    <div className="fit-section__item">
                                        <div className="img-wrap">
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/fit_img_2.jpg`} alt="" className="img" />
                                        </div>
                                        <div className="fit-section__title">
                                            <br />
                                            <span className="gray"> ,<br />    </span>
                                        </div>
                                    </div>
                                    <div className="fit-section__item">
                                        <div className="img-wrap">
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/fit_img_3.jpg`} alt="" className="img" />
                                        </div>
                                        <div className="fit-section__desc">
                                                ,      .<br />
                                                    ,     .
                                        </div>
                                    </div>
                                </div>

                                <div className="fit-section elem_animate up">
                                    <div className="fit-section__item">
                                        <div className="img-wrap">
                                            <img src={`${process.env.PUBLIC_URL}/imgs/main/fit_img_4.jpg`} alt="" className="img" />
                                        </div>
                                        <div className="text-wrap">
                                            <div className="fit-section__title">
                                                   <br />
                                                <span className="gray"> </span>
                                            </div>
                                            <div className="fit-section__desc">
                                                    ,  ,    .<br />
                                                   ,         .
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                {/* Section -    */}
                <section className="main-sec finance-service">
                    <div className="inner">
                        <div className="main-sec__wrap">
                            <h1 className="main-sec__title elem_animate up">  </h1>
                            <p className="main-sec__desc elem_animate up">
                                  <br />
                                 
                            </p>

                            <div className="credit__content-wrap">
                                <div className="credit__layout elem_animate up">
                                    <div className="credit__layout-item">
                                        <img src={`${process.env.PUBLIC_URL}/imgs/main/finance_service_img_1.png`} alt="finance service 1" className="credit__layout-img" />
                                        <p className="credit__layout-title">  </p>
                                        <p className="credit__layout-desc">
                                                .<br />
                                                  <br />
                                             .
                                        </p>
                                    </div>
                                    <div className="credit__layout-item">
                                        <img src={`${process.env.PUBLIC_URL}/imgs/main/finance_service_img_2.png`} alt="finance service 2" className="credit__layout-img" />
                                        <p className="credit__layout-title">  </p>
                                        <p className="credit__layout-desc">
                                            ,    .<br />
                                                      .
                                        </p>
                                    </div>
                                    <div className="credit__layout-item">
                                        <img src={`${process.env.PUBLIC_URL}/imgs/main/finance_service_img_3.png`} alt="finance service 3" className="credit__layout-img" />
                                        <p className="credit__layout-title"></p>
                                        <p className="credit__layout-desc">
                                                .<br />
                                              5,900     4% ,    1    .<br />
                                            (20 4%, 20~60 1%   )
                                        </p>
                                    </div>
                                    <div className="credit__layout-item">
                                        <img src={`${process.env.PUBLIC_URL}/imgs/main/finance_service_img_4.png`} alt="finance service 4" className="credit__layout-img" />
                                        <p className="credit__layout-title">ATM  </p>
                                        <p className="credit__layout-desc">
                                                 .<br />
                                               ATM    .
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                {/* Section -    */}
                <section className="main-sec business">
                    <div className="business__img-wrap elem_animate up">
                        <img src={`${process.env.PUBLIC_URL}/imgs/main/business_img_1.jpg`} alt="" />
                    </div>

                    <div className="business__desc elem_animate up">
                        <h4 className="business__desc__title">  </h4>
                        <p className="business__desc__small">
                             ?<br />
                              <br />
                               .
                        </p>
                    </div>

                    <div className="business__link-wrap elem_animate up">
                        <ul className="business__link-list">
                            <li className="business__link-item">
                                <h5 className="business__link-title"></h5>
                                <p className="business__link-desc">
                                     ,<br />
                                       <br />
                                      .
                                </p>
                                <a href="/clone_toss_react" className="business__link-btn"> </a>
                            </li>
                            <li className="business__link-item">
                                <h5 className="business__link-title">  </h5>
                                <p className="business__link-desc">
                                          .<br />
                                     ,  ,     .
                                </p>
                                <a href="/clone_toss_react" className="business__link-btn"> </a>
                            </li>
                            <li className="business__link-item">
                                <h5 className="business__link-title"></h5>
                                <p className="business__link-desc">
                                       ,<br />
                                      <br />
                                     .
                                </p>
                                <a href="/clone_toss_react" className="business__link-btn"> </a>
                            </li>
                            <li className="business__link-item">
                                <h5 className="business__link-title"></h5>
                                <p className="business__link-desc">
                                     <br />
                                      <br />
                                       .
                                </p>
                                <a href="/clone_toss_react" className="business__link-btn"> </a>
                            </li>
                        </ul>
                    </div>
                </section>
            </main>

            {/* Footer */}
            <Footer></Footer>
        </div >
    );
}

export default Home;
