function Footer() {
    const gnb = [
        ['', [
            '', '  ', ' ', '  ', '', ' ', '  ', '', '  '
        ]],
        ['', [
            ' ', '', '', '', '', '', '', '', '', '', ''
        ]],
        ['', [
            ' ', ' ', ' ', '  ', 'PR', 'IR'
        ]],
        ['', [
            ': 0000-0000(24 )', '(): abcde@oooo.oo',
            '(): abcde@oooo.oo', ' '
        ]]
    ];

    const terms = [
        ' ', '   ', '  ', '   ', ' ', '  ', '   ', '   ', ' ', ' ', ' ', '  ', ''
    ];

    return (
        <footer className="footer">
            <div className="inner">

                {/* footer gnb */}
                <div className="f-gnb">
                    <ul className="f-gnb__list">
                        {gnb.map((el, idx) => {
                            let depth1 = el[0];
                            let depth2 = el[1];

                            let listElem = (
                                <li key={idx} className="f-gnb__item">
                                    <h4 className="f-gnb__dep1">{depth1}</h4>
                                    <ul className="f-gnb__sub-list">
                                        {
                                            depth2.map((el2, idx2) => {
                                                return (
                                                    <li key={idx2} className="f-gnb__sub-item">
                                                        <a href="/clone_toss_react">{el2}</a>
                                                    </li>
                                                )
                                            })
                                        }
                                    </ul>
                                </li>
                            );
                            return listElem;
                        })}
                    </ul>

                    {/* company info */}
                    <div className="company-info">
                        <div className="company-info__title"> T O S S</div>
                        <address className="company-info__address">
                              : 000-00-000000 |  : OOO<br />
                              :  OOOOOO |   : 0000-AA-000000 <a href="/clone_toss_react" className="chk-business-info"></a><br />
                        </address>
                    </div>

                    {/* terms info */}
                    <div className="terms-info">
                        <ul className="terms-info__list">
                            {terms.map((term, idx) => {
                                let strongIdx = [4, 5, 6, 8];
                                let strong = strongIdx.includes(idx) ? 'strong' : '';
                                let el = (
                                    <li key={idx} className={"terms-info__item " + strong}>
                                        <a href="/clone_toss_react">{term}</a>
                                    </li>
                                )
                                return el;
                            })}
                        </ul>
                    </div>

                    {/* sns */}
                    <div className="sns-info">
                        <ul className="sns-info__list">
                            <li>
                                <a href="/clone_toss_react">
                                    <img src={`${process.env.PUBLIC_URL}/imgs/footer/icn-facebook.svg`} />
                                </a>
                            </li>
                            <li>
                                <a href="/clone_toss_react">
                                    <img src={`${process.env.PUBLIC_URL}/imgs/footer/icn-blog.svg`} />
                                </a>
                            </li>
                            <li>
                                <a href="/clone_toss_react">
                                    <img src={`${process.env.PUBLIC_URL}/imgs/footer/icn-naver.svg`} />
                                </a>
                            </li>
                            <li>
                                <a href="/clone_toss_react">
                                    <img src={`${process.env.PUBLIC_URL}/imgs/footer/icn-twitter.svg`} />
                                </a>
                            </li>
                            <li>
                                <a href="/clone_toss_react">
                                    <img src={`${process.env.PUBLIC_URL}/imgs/footer/icn-instagram.svg`} />
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </footer >
    );
}

export default Footer;